.App {
  text-align: center;
}

.Holder {
  margin: 15px !important;
  box-shadow: 5px 5px 8px black;
  background-color: black;
}

.input-box {
  position: fixed;
  z-index: 3;
  width: 20%;
  height: auto;
  margin: 10px;
  top: 10%;
  left: 40%;
  background-color: white;
  padding-bottom: 20px;
  border-radius: 5px;
  opacity: 50%;
}

img {
  object-fit: cover;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}

html {
  background-image: linear-gradient(red, blue);
}
